@use '../helpers' as h;

.page{
    padding-top: 73px;
    background-color: #F5F5F5 ;
}
.header-part{
    // min-height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 158px;
    position: relative;
    z-index: 1;
    margin-bottom: 60px;
    .h1{
        font-family: 'Circe';
        font-size: 29px;
        line-height: 110%;
        position: relative;
        font-weight: 400;
        width: 80%;
        // top:50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        text-align: center;
        z-index: 3;
    }
    .blue-block {
        position: absolute;
        background: linear-gradient(#00A5EC, #06202b);
        height: 100%;
        width: 100%;
        opacity: 0.8;
        z-index: 2;
    }
}

.container-main{
    min-width: 300px;
    padding: 15px 29px;
    margin: 0 auto;
}

.card{
    &-block{

    }
    &-link{
        display: flex;
        justify-content: space-between;
        flex-direction: column;   
        background-color: #ffff;
        border-radius: 20px;     
        margin-bottom: 20px;
        position: relative;
        display: block;
        padding: 40px 15px 27px 34px;
    }
    &-text-block{
        .h2{
            font-weight: 400;
            margin-bottom: 30px;
            text-align: left;
            line-height: 130%;
            display: block;
            transition: color ease 0.3s;
            cursor: pointer;
            &:hover{
                color: h.$accent-color;
            }
        }
        .standart-text{
            margin-bottom: 28px;
            color: #636363;
        }
    }
    &-icon-place{
        position: absolute;
        right: 30px;
        bottom: 30px;
        width: 38px;
        height: 38px;
    }
}

.pseudo-link{
    display: block;
    font-size: 20px;
    color: h.$accent-color;
}

.padding-wrap{
    padding: 62px 0 19px;
}

.text-block{
    margin-bottom: 50px;
    &:last-child{
        margin-bottom: 100px;;
    }
}



.dots-list{
    padding-top: 14px;;

    li{
        position: relative;
        padding-left: 30px;
        margin-bottom: 25px;
    }
    li:before {
        content: "";
        display: block;
        position: absolute;
        left: 0px;
        top: 8px;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background-color: h.$accent-color;
    }
}


blockquote{
    font-family: inherit;
    font-weight: 300;
    font-size: 20px;
    line-height: 157%;
    text-align: center;
    padding: 119px 15px 50px 15px ;
    position: relative;
    &:before{
        content: "";
        display: block;
        position: absolute;
        width:52px;
        height:51px;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url(../img/icons/quote.svg);
        // left: 0px;
        top: 41px;
        left: 50%;
        transform: translateX(-50%);
    }
}

.text-link{
    transition: color ease 0.3s;
    position: relative;
    &:after{
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0px;
        bottom: 1px;
        background-color: #000;
        transition: width ease 0.3s;
    }
    // text-decoration: underline;
    &:hover{
        color: h.$accent-color;
        &:after{
            width: 0%;
        }
    }
}

.accordion {
    cursor: pointer;
    transition: 0.4s;
    transition: .4s;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    justify-content: flex-start;
    .text{
        font-weight: 300;
        width: 84%;
        padding-right: 0px;
        padding-left: 35px;
    }

    &.card-link{
        padding: 30px 26px 36px 32px;
        margin-bottom: 00px;;
        z-index: 5;
    }

  }
  
  .active, .accordion:hover {

  }
  
  .accordion:after {
    content: '';  
    position: absolute;
    right: 20px;
    top: 46px;
    font-weight: bold;
    margin-left: 5px;
    width: 20px;
    height: 1px;
    transition: all ease 0.3s;
    background-color: #000;    
  }

  .accordion:before {
    content: '';  
    position: absolute;
    right: 20px;
    top: 40%;
    font-weight: bold;
    margin-left: 5px;   
    height: 20px;
    width: 1px;
    right: 30px;
    top: 36px;
    transition: all ease 0.3s;
    background-color: #000;    
  }
  .wrap-tab.active{

      .accordion:before {
            transform: rotate(90deg);
            // right: 30px;
            // top: 36px;
      }
      .text:first-child{
        color: h.$accent-color

      }
  }
  
  .accordion.active {
    .panel{
        // padding: 32px 18px 45px; 
        padding: 48px 21px 0 53px;
        padding-bottom: 44px;
    }
  }

  .wrap-tab.inner-tab{
    .accordion.card-link {
        padding: 20px 26px 20px 04px;
    }
    .accordion .text {
        font-size: 32px;
        padding-right: 116px;
        width: 99%;
    }
    .accordion:after {
        top: 45px;
    }
    .accordion:before{
        top: 26px;
    }
  }


  .panel {
    background-color: white;
    color: #000;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease-out;
    // padding: 48px 21px 0 53px;
    padding: 8px 21px 0 53px;
    transform: translateY(-50px);
    border-radius: 20px;
    z-index: -1;
    transition: all ease 0.3s;;
    p.standart-text{
        color: #000;
        font-weight: 300;
    }
    .dots-list li:before{
        height: 5px;
        width: 5px;
        top: 11px;
    }
    .dots-list li {
        margin-bottom: 15px;;
        padding-left: 20px;
    }
  }
  .wrap-tab.active{

      .panel{
          padding: 48px 21px 0 53px;
      }
  }



  .tabs.cards{
    padding: 3px 0px;
  }


.tabs.cards {
    counter-reset: tab-counter;
    margin-bottom: 50px;;
}

.wrap-tab {
    counter-increment: tab-counter;
    margin-bottom: 20px;
    .icon-money{
        width: 25px;
        height: 25px;
        margin-left: -4px;
        margin-right: 4px;
    }
}

.wrap-tab .accordion.card-link .number::before {
    content: counter(tab-counter) " ";
    font-weight: bold;
    font-family: h.$font-family;
    font-size: 20px;
    color: h.$accent-color;
}


.lists-wrap {
    counter-reset: tab-counter;
}

.list-item {
    counter-increment: tab-counter;
    position: relative;
    padding-left: 20px;;
}

.lists-wrap .list-item:before {
    content: counter(tab-counter) ". ";
    position: absolute;
    left: 0px;
}

.accordi {
    // background-color: #eee;?
    color: #444;
    cursor: pointer;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    transition: 0.4s;
  }
  
  /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
  .inner-tab.active, .accordi:hover {
    background-color: #ccc;
  }
  
  /* Style the accordion panel. Note: hidden by default */
  .pane {
    padding: 0 18px;
    background-color: white;
    // display: none;
    margin-bottom: 40px;;
    overflow: hidden;
  }

  .inner-tab .accordi{
    text {
        color: #000;
        font-size: 24px;
     }
    }

    .inner-tab .accordi{
        text {
            color: #000;
            font-size: 24px;
        }
    }

