/* header */
.header {
  width: 100%;
  z-index: 33;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 25px 17px;
  position: fixed;
  margin-bottom: 7px;
  background-color: #FFF;
  transform: translateY(0%);
}

@keyframes slideOut {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0%);
  }
}
.logo-place {
  width: 154px;
}
.logo-place .logo img {
  width: 100%;
}

#nav-icon {
  width: 32px;
  height: 21px;
  position: relative;
  margin: 0px 5px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #000000;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

#nav-icon span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon span:nth-child(2) {
  top: 8px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon span:nth-child(3) {
  top: 16px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -3px;
  left: 8px;
}

#nav-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  left: 7px;
  top: 21px;
  width: 33px;
}

.fadeInLeft {
  animation: fadeInLeft 1s ease forwards;
}

.scroll {
  animation-timing-function: linear;
}

.header-menu {
  position: absolute;
  top: 69px;
  left: 0px;
  width: 100%;
  transform: translateX(-100%);
  transition: all ease 0.4s;
  background-color: #FFF;
  z-index: 9;
  padding: 20px 25px;
}
.header-menu_item {
  padding: 21px 0px 17px;
  border-top: 1px solid #EAEAEA;
  position: relative;
  transition: color ease 0.3s;
  animation-timing-function: ease;
  animation-duration: 800ms;
}
.header-menu_item:nth-child(1) {
  animation-delay: 900ms;
}
.header-menu_item:nth-child(2) {
  animation-delay: 800ms;
}
.header-menu_item:nth-child(3) {
  animation-delay: 700ms;
}
.header-menu_item:nth-child(4) {
  animation-delay: 600ms;
}
.header-menu_item:nth-child(5) {
  animation-delay: 500ms;
}
.header-menu_item:nth-child(6) {
  animation-delay: 400ms;
}
.header-menu_item:nth-child(7) {
  animation-delay: 300ms;
}
.header-menu_item:nth-child(8) {
  animation-delay: 10ms;
}
.header-menu_item.has_childrens:after {
  content: "";
  position: absolute;
  height: 20px;
  right: 10px;
  top: 18px;
  width: 21px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('data:image/svg+xml,<svg width="20" height="16" viewBox="0 0 20 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 7C0.447715 7 0 7.44772 0 8C0 8.55228 0.447715 9 1 9V7ZM19.7071 8.70711C20.0976 8.31658 20.0976 7.68342 19.7071 7.29289L13.3431 0.928932C12.9526 0.538408 12.3195 0.538408 11.9289 0.928932C11.5384 1.31946 11.5384 1.95262 11.9289 2.34315L17.5858 8L11.9289 13.6569C11.5384 14.0474 11.5384 14.6805 11.9289 15.0711C12.3195 15.4616 12.9526 15.4616 13.3431 15.0711L19.7071 8.70711ZM1 9H19V7H1V9Z" fill="%23303030"/></svg>');
}
.header-menu_item .sub-menu li a {
  color: #000000;
}
.header-menu_item.active {
  color: #12A1DC;
}
.header-menu_item:hover {
  color: #12A1DC;
}
.header-menu_item:hover .sub-menu li a:hover,
.header-menu_item:hover .sub-menu li.active a {
